<template>
  <v-container class="container-3d k-absolute k-x-transform-center">

    <v-card class="mb-16 mx-auto roll-180-x" width="420" tile elevation="10">
      <v-card
          tile
          height="720px"
          elevation="0"
          class="height-shrink k-overflow-hidden"
      >
        <img
            class="k-absolute img-move k-top-0"
            src="../../assets/index/logincard/BuradirenaMiriize.jpg"
        />
      </v-card>

      <v-form
          ref="formQuery"
          method="post"
          action="/demo1"
          v-show="this.$store.state.displayLogin"
          v-model="valid1"
          class="px-8 py-6 form-class"
      >

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-lumx"
            background-color="transparent"
            clear-icon="mdi-close"
            counter
            maxlength="6"
            clearable
            color="light-blue darken-3"
            hide-details="auto"
            label="ID"
            :rules="[rules.loginID]"
            type="text"
            v-model="queryData.queryId"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-lumx"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="light-blue darken-3"
            hide-details="auto"
            label=""
            type="password"
            v-model="queryData.queryPwd"
        ></v-text-field>

        <v-btn
            dark
            type="button"
            @click="query"
        >
          
        </v-btn>

        <a
            @click="showR"
            class="ml-8"
            href="javascript:void(0);"
        >
          <span> >> </span>
        </a>

      </v-form>

      <v-form
          ref="formRegister"
          v-show="this.$store.state.displayRegister"
          class="px-8 py-6 form-class"
          method="post"
          action="/demo2"
      >

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-pen-minus"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="light-blue darken-3"
            hide-details="auto"
            label=""
            :rules="[rules.required,rules.counter]"
            type="text"
            v-model="registerData.userName"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-lock-check"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="deep-purple lighten-1"
            hide-details="auto"
            label=""
            v-model="registerData.password"
            :rules="[rules.pwdRegister]"
            type="password"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-lock-pattern"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="green"
            hide-details="auto"
            label=""
            v-model="registerData.pwdCheck"
            :rules="[checkPwd]"
            type="password"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-gmail"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="orange darken-2"
            hide-details="auto"
            label=""
            :rules="[rules.email]"
            type="text"
            v-model="registerData.mailAddress"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-file-document-edit-outline"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="purple lighten-1"
            hide-details="auto"
            label=""
            :rules="[rules.contactor]"
            type="text"
            v-model="registerData.feedbackMethod"
        ></v-text-field>

        <v-text-field
            class="pb-4"
            prepend-inner-icon="mdi-calendar-clock"
            background-color="transparent"
            clear-icon="mdi-close"
            clearable
            color="green darken-3"
            hide-details="auto"
            label=""
            :rules="[rules.deadline]"
            type="text"
            v-model="registerData.deadLine"
        ></v-text-field>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="black"
            label=""
            type="radio"
            :items="transactionTypes"
            item-text="type"
            item-value="abbr"
            v-model="registerData.illustType"
            :rules="[rules.illustType]"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="black"
            label=""
            type="radio"
            :items="backgroundRequired"
            item-text="type"
            item-value="abbr"
            v-model="registerData.background"
            :rules="[rules.background]"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="indigo darken-3"
            label="CNY"
            type="radio"
            :items="budget"
            item-text="type"
            item-value="type"
            v-model="registerData.budget"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="indigo darken-3"
            label=""
            type="radio"
            :items="authorizedRelease"
            item-text="type"
            item-value="abbr"
            v-model="registerData.authorizedRelease"
            :rules="[rules.authorizedRelease]"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="indigo darken-3"
            label=""
            type="radio"
            :items="maskType"
            item-text="type"
            item-value="abbr"
            v-model="registerData.maskType"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="indigo darken-3"
            label=""
            type="radio"
            multiple
            chips
            :items="paymentInstrument"
            item-text="type"
            item-value="abbr"
            v-model="registerData.paymentInstrument"
        ></v-select>

        <v-select
            background-color="transparent"
            prepend-inner-icon="mdi-drag-variant"
            color="purple"
            label=""
            type="radio"
            multiple
            chips
            :items="imgFormat"
            item-text="type"
            item-value="abbr"
            v-model="registerData.imgFormat"
            :rules="[rules.imgFormat]"
        ></v-select>

        <v-textarea
            v-model="registerData.comments"
            type="text"
            prepend-inner-icon="mdi-archive-edit-outline"
            label="/"
            counter
            maxlength="2000"
            color="brown darken-2"
            background-color="transparent"
            placeholder=""
        >

        </v-textarea>

        <v-radio-group>
          <v-radio
              class="text-body-2"
              label=''
              color="red darken-3"
              value="yes"
              @click="know=false"
          ></v-radio>
        </v-radio-group>

        <v-btn
            color="grey darken-4"
            class="white--text"
            type="button"
            @click="register"
            :disabled="know"
        >
          
        </v-btn>

        <a
            @click="showL"
            class="ml-8"
            href="javascript:void(0);"
        >
          <span> >> </span>
        </a>

      </v-form>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginPage",

  data: () => ({
    submitDisabled: false,
    pwd1: "",
    pwd2: "",
    valid1: true,
    valid2: true,
    know:true,

    transactionTypes: [
      {type: '', abbr: 'Illustration'},
      {type: '', abbr: 'Illustration'},
      {type: '', abbr: 'ThreeView'},
      {type: '', abbr: 'HalfBody'},
      {type: '', abbr: 'Avatar'},
      {type: 'Live2D', abbr: 'L2D'},
      {type: '//', abbr: 'Comic'},
      {type: 'UI', abbr: 'UI'},
      {type: 'GIF', abbr: 'DynamicImg'},
    ],

    backgroundRequired: [
      {type: '', abbr: 'background-1'},
      {type: '', abbr: 'background-0'},
      {type: '   ', abbr: 'background-2'},
    ],

    budget: [
      {type: '10-50'},
      {type: '50-100'},
      {type: '100-200'},
      {type: '200-500'},
      {type: '500-1000'},
      {type: '1000-2000'},
      {type: '2000+'}
    ],

    imgFormat: [
      {type: "jpg", abbr: "jpg"},
      {type: "png", abbr: "png"},
      {type: "gif", abbr: "gif"},
      {type: "psd", abbr: "psd"},
      {type: "sai", abbr: "sai"},
      {type: "csp", abbr: "csp"},
    ],

    authorizedRelease: [
      {type: "", abbr: 'prohibit'},
      {type: "", abbr: 'consult'},
      {type: "", abbr: 'freedom'}
    ],

    maskType: [
      {type: "", abbr: 'no'},
      {type: "", abbr: 'light'},
      {type: "", abbr: 'moderate'},
      {type: "", abbr: 'severe'}
    ],

    paymentInstrument: [
      {type: "", abbr: "wxPay"},
      {type: "", abbr: "aliPay"},
      {type: "", abbr: "p2p"},
      {type: "paypal", abbr: "paypal"},
      {type: "line pay", abbr: "line"},
      {type: "", abbr: "else"},
    ],

    registerData: {
      userName: "",
      password: "",
      pwdCheck: "",
      mailAddress: "",
      feedbackMethod: "",
      deadLine: "",
      illustType: "",
      background: "",
      budget: "",
      authorizedRelease: "",
      maskType: "",
      paymentInstrument: "",
      imgFormat: "",
      comments: "",
    },

    queryData: {
      queryId: "",
      queryPwd: ""
    },

    rules: {
      required: value => !!value || '',
      illustType:value=>!!value||'',
      background:value=>!!value||'',
      authorizedRelease:value=>!!value||'',
      imgFormat:value=>!!value||'',
      counter: value => value.length <= 20 || '20',
      email: value => {
        const pattern = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
        return pattern.test(value) || '123abc@abc.com'
      },
      contactor: value => {
        const cPattern = /^[^\s]{1,10}=[^\s]{1,20}$/
        return cPattern.test(value) || "=123456789"
      },
      deadline: value => {
        const dPattern = /^\d{4}-\d{1,2}-\d{1,2}\s\d{1,2}:\d{1,2}:\d{1,2}$/
        return dPattern.test(value) || "2022-1-1 18:00:00"
      },
      pwdRegister: value => {
        const pRPattern = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9]{8,16}$/
        return pRPattern.test(value) || " 8-16 "
      },
      loginID: value => {
        const idPattern = /^\d{6}$/
        return idPattern.test(value) || "123456"
      },
    }
  }),

  methods: {
    checkPwd() {
      if (this.$data.registerData.password
          != this.$data.registerData.pwdCheck)
        return ""
      else
        return true
    },
    register() {
      if (this.$refs.formRegister.validate()) {
        axios({
          url: "/register",
          method: "post",
          baseURL: "http://localhost/v4/",
          data: this.$data.registerData
        }).then((res)=>{

        }).catch((err)=>{
          console.log('')
        })
      }
    },
    query() {
      if (this.$refs.formQuery.validate()) {
        axios({
          url: "/login",
          method: "post",
          baseURL: "http://localhost/v4/",
          data: this.$data.queryData
        }).then((res)=>{

        }).catch((err)=>{
          console.log('')
        })
      }
    },
    showL(){
      this.$store.commit('showLogin')
    },
    showR(){
      this.$store.commit('showRegister')
    }
  }
}
</script>

<style scoped>
@keyframes img-moving {
  from {
    transform: translateY(0%);
  }
  to {
    transform: translateY(-14%);
  }
}

@keyframes img-height-shrink {
  to {
    height: 110px;
  }
}

.height-shrink {
  animation: 1.5s cubic-bezier(.38, .05, .41, .94) forwards img-height-shrink;
}

.img-move {
  width: 420px;
  animation: 30s linear .4s forwards img-moving;
}

.form-class {
  font-family: "-85W", sans-serif;
}

.container-3d {
  transform-style: preserve-3d;
  perspective: 5000px;
}
</style>











